@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;400&display=swap');
@import url('https://fonts.googleapis.com/css?family=Ephesis&display=swap&effect=shadow-multiple|anaglyph');
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100%;
  font-family: 'Josefin Sans', sans-serif;
}

header{
  width: 100%;
  height: 10vh;
  padding: 15px 100px;
  background: #2c2c2c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  letter-spacing: 2px;
}
header .logo{
  font-family: 'Ephesis', cursive;
  font-weight: 200;
  font-size: 2.9em;
}
header input{
  border: none;
  outline: none;
  height: 30px;
  width: 180px;
  border-radius: 30px;
  background: #afadad36;
  padding: 0 10px;
  font-family: inherit;
  color: aliceblue;
}

@media screen and (max-width:700px) {
  header{
    padding: 15px 50px;
  }
}
@media screen and (max-width:500px) {
  header{
    padding: 15px 10px;
  }
}