::selection{
    background: #08ff08;
    color: black;
}
article{
    width: 100%;
    height: 90vh;
    background-blend-mode: soft-light;
    backdrop-filter: blur(8px);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
article::before{
    position: absolute;
    content: '';
    inset: 0;
    z-index: -1;
    backdrop-filter: blur(4px);
}
.card{
    width: 800px;
    height: 450px;
    display: flex;
    justify-content: center;
    background: #960101af;
    box-shadow: 0 0 50px rgba(0,0,0,.9),
    0 0 100px rgba(0,0,0,.7),
    0 0 150px rgba(0,0,0,.5);
}

.card .imgBox{
    width: 300px;
    height: 100%;
}
.card .imgBox img{
    height: 100%;
    object-fit: cover;
}

.card .contentBox{
    width: 500px;
    box-sizing: border-box;
    padding: 2em 1.5em;
    background: rgb(44, 44, 44);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card .contentBox h1{
    font-size: 2.2em;
    color: #fc0e34;
    text-shadow: 2px 2px 2px rgb(247, 70, 0);
}

.card .contentBox .sub-title{
    width: 80%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-shadow: 1px 0px 0px black;
}
.card .contentBox .sub-title h3{
    font-size: 1em;
    font-weight: 200;
    color: rgb(236, 236, 236);
}
.card .contentBox .sub-title h3:nth-child(1){
    color: #0ff0fc;
}
.card .contentBox .sub-title h3:nth-child(2){
    color: #08ff08;
}
.card .contentBox .sub-title h3:nth-child(3){
    color: #ccff00;
}

.card .contentBox p.plot{
    font-size: 1.2em;
    font-weight: 400;
    color: #01f9c6;
    text-shadow: 2px 2px 5px rgb(247, 70, 0);
}

.card .contentBox .ratings{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 80px;
    /* background: chocolate; */
    position: relative;
}
.card .contentBox .ratings::before{
    position: absolute;
    content: '';
    width: 4px;
    height: 70%;
    background: rgb(167, 167, 167);
    top: 50%;
    transform: translateY(-50%);
    left: calc(47% - 2px);
    filter: blur(2px);
}

.card .contentBox .ratings div{
    /* background: tomato; */
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.card .contentBox .ratings div span{
    font-weight: 200;
    font-size: .8em;
}
.card .contentBox .ratings div h4{
    font-size: 2em;
    font-family: 'Montserrat', sans-serif;
    text-shadow: 2px 2px 5px antiquewhite;
}
