@media screen and (max-width:850px) {
    article {
        width: 100%;
        height: 90vh;
        padding: .5em;
        background-blend-mode: soft-light;
        backdrop-filter: blur(8px);
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    .card {
        width: 100%;
        height: 450px;
        display: flex;
        justify-content: center;
        background: #960101af;
        box-shadow: 0 0 50px rgba(0, 0, 0, .9), 0 0 100px rgba(0, 0, 0, .7), 0 0 150px rgba(0, 0, 0, .5);
    }
}

@media screen and (max-width:700px) {
    .card .contentBox .ratings::before {
        display: none;
    }
}

@media screen and (max-width:600px) {
    article {
        width: 100%;
        height: unset;
        padding: 1em;
        background-blend-mode: soft-light;
        backdrop-filter: blur(8px);
        background-size: 50%;
        color: white;
        display: block;
        position: relative;
    }
    .card {
        width: 100%;
        height: unset;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        background: none;
        box-shadow: 0 0 50px rgba(0, 0, 0, .9), 0 0 100px rgba(0, 0, 0, .7), 0 0 150px rgba(0, 0, 0, .5);
        box-shadow: none;
    }
    .card .imgBox {
        width: 100%;
        height: unset;
    }
    .card .imgBox img {
        width: 100%;
        height: unset;
        object-fit: cover;
    }
    .card .contentBox {
        margin-top: -5px;
        z-index: 5;
        height: 100%;
        width: 100%;
        box-sizing: border-box;
        padding: 1em .5em;
        background: rgb(44, 44, 44);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .card .contentBox>*{
        margin-bottom: 1em;
    }
    .card .contentBox .sub-title {
        width: 100%;
        height: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .card .contentBox .ratings{
        margin-top: 1em;
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 60px;
        /* background: chocolate; */
        position: relative;
    }
}